/* Стили кнопки */
.button {
    display: inline-block;
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    background-color: #4CAF50;
    color: white;
    font-size: 16px;
    text-align: center;
    text-decoration: none;
    cursor: pointer;
  }
  
  /* Стили при наведении на кнопку */
  .button:hover {
    background-color: #a05645;
    transition: 0.3s;
  }
  
  /* Стили при нажатии на кнопку */
  .button:active {
    background-color: #653e8e; /* Замените на желаемый цвет фона при нажатии */
  }