input {
    padding: 10px;
    border: none;
    border-radius: 5px;
    background-color: #f2f2f2;
    box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.1);
    font-size: 16px;
    color: #333333;
    transition: background-color 0.3s ease;
  }
  
  input:focus {
    outline: none;
    background-color: #e8e8e8;
  }
  
  input::placeholder {
    color: #999999;
  }