.form {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-left: 15%;
    margin-top: 10%;
  }
  
  .Formss {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  
  .blockleft,
  .blockright {
    width: calc(50% - 15px); /* Разделение блоков на две колонки с отступом */
    margin-bottom: 20px;
  }
  
  h3 {
    font-weight: bold;
  }
  
  
  
  .Formss::before {
    content: ""; /* Создание разделителя между блоками */
    display: block;
    width: 100%;
    height: 1px;
    background-color: #ccc;
    margin-bottom: 20px;
  }