.select {
    width: 50%;
    padding: 8px;
    border: 1px solid #ccc;
    border-radius: 4px;
    background-color: #fff;
    appearance: none;
    background-repeat: no-repeat;
    background-position: right center;
    height: 6%;
  }
  
  .select:focus {
    outline: none;
    border-color: #4CAF50;
    box-shadow: 0 0 5px rgba(76, 175, 80, 0.5); /* Добавление эффекта тени при фокусе */
  }